.footer {
  padding: 6.4rem 0 2.4rem;
  background-color: #060649;
}

.footer-container {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid darkgoldenrod;
  margin-bottom: 2.4rem;
}

/* ########################################################################## */

.footer-community {
  display: flex;
  flex-direction: column;
  flex-basis: 40rem;
}

.footer-description {
  font-size: 1.6rem;
  line-height: 1.2;
  margin-bottom: 3.2rem;
}

.footer-join {
  color: #fff;
  font-size: 1.6rem;
  font-weight: 500;
  margin-bottom: 1.6rem;
}

.networks-list {
  list-style: none;
  display: flex;
  gap: 2.4rem;
}

.network-icon {
  height: 2.4rem;
  width: 2.4rem;
  pointer-events: none;
}

/* ########################################################################## */

.footer-grid {
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 9.6rem;
  padding-bottom: 6.4rem;
}

.footer-heading {
  font-size: 1.8rem;
  font-weight: 500;
  margin-bottom: 3.2rem;
}

.footer-list {
  font-size: 1.6rem;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
}

.network-link:link,
.footer-link:link,
.network-link:visited,
.footer-link:visited {
  display: inline-block;
  text-decoration: none;
  color: goldenrod;
  transition: all 0.3s;
}

.network-link:hover,
.footer-link:hover,
.network-link:active,
.footer-link:active {
  color: darkgoldenrod;
}

/* ########################################################################## */

.copyright-container {
  display: flex;
  justify-content: space-between;
  font-size: 1.2rem;
}

.legal-documents {
  display: flex;
  gap: 2.4rem;
}

.legal-doc-link {
  text-decoration: none;
  color: #ded6d6;
}
